<template>
  <div  class="about">
    <v-container >
      <v-row  g-1 >
        <v-col cols="12" md="3" sm="6" >
            <v-text-field
            label="Caminho"
            :loading="on"
            :disabled="on"
            
            append-outer-icon="mdi-keyboard-return"
            append-icon="mdi-restart"
            prepend-icon="mdi-cloud-search"
            :hint="`Pasta atual: ${curDir}`"

              counter
               @click:append-outer="goBack(dir)"
              @click:append="onRestart(restart)"
            
            @change="search(dir)"
            v-model="dir"
            outlined>
          </v-text-field>
        </v-col>
        <v-col cols="12" md="9" sm="9" >
        <v-card
        :loading="on"
        :disabled="on"
         elevation="2"   outlined >

        <v-simple-table  
         fixed-header
        height="400px"
        dense
        
        :loading="on"
        
        >
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">
                  Total: {{ archives.length }}
                </th>
                <th class="text-left">
                  Nome
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item) in archives"
                :key="item.name"  >
                <td
                    class="pa-2"

                >
                  <v-menu
              transition="slide-x-transition"
              bottom
              right
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                v-bind="attrs"
                 v-on="on"
                elevation="4"
                color="primary"
                >
                  <v-icon>
                  mdi-menu
                  </v-icon>
              </v-btn>
     
              </template>
                   <v-list>
          <v-list-item>
            <v-list-item-avatar>

              <v-icon>{{item.isFile ? 'mdi-file-outline' : 'mdi-folder'}}</v-icon>
              
           
            </v-list-item-avatar>

            <v-list-item-content>
              <v-list-item-title>{{item.name}}</v-list-item-title>
              <v-list-item-subtitle></v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
        </v-list>

        <v-divider></v-divider>

              <v-list dense >
                <v-list-item  @click="item.isFile ? download(item.name) : searchIn(item.name)">
                  <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>
                  <v-list-item-title
                  >{{item.isFile ? 'Baixar' : 'Entrar'}}
                  </v-list-item-title>
                </v-list-item>

                <v-list-item @click="info(`${dir}/${item.name}`, item)" >
                  <v-list-item-icon>
              <v-icon>{{ 'mdi-information-variant' }}</v-icon>
               </v-list-item-icon>
                  <v-list-item-title>
                  Detalhes
                  </v-list-item-title>
                </v-list-item>


                <v-list-item >
                  <v-list-item-icon>
            <v-icon>{{ 'mdi-trash-can' }}</v-icon>
          </v-list-item-icon>
                  <v-list-item-title
                  >Apagar
                  </v-list-item-title>
                </v-list-item>


              </v-list>
            </v-menu>
            
                </td>
                <td>
                 {{ item.name }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
        </v-card>

        </v-col>
      </v-row>
    </v-container>

  </div>
</template>
<script>
import axios from 'axios'
import ip from '../assets/js/endereco'

  export default {
    data () {
      return {
      archives:[],
      dir:'',
      on:false,
      restart:'',
      curDir:'',
      menu: [

      ],
    }
    },

    methods: {

      async info(item, pack){
        try{

         const data = await this.getItemInfo(item) 

          this.$FModal.show({
        component: () => import('@/components/HelloWorld.vue') },
        { 
          
          data:pack,
          desc: data
        
        })


        } catch(err) {
          
          console.log(err)

        }

      },
      getItemInfo(item){
       
       return new Promise(async (resolve,reject)=>{

        const data = (await axios.get(`${ip}/dir/info`,{params:{item}})).data
         
    //    console.log(data) 
        resolve(data)


       })

      },
      async goBack(dir){


       const back = dir.replace(`${this.curDir}`,``)
      
        await this.search(back)
      
  


      },
      getDirData(){
        try {
          return new Promise(async (resolve,reject)=>{
            
          const data = (await axios.get(`${ip}/dir`)).data
         
         
         resolve(data)



        })
            
          } catch (error) {
            alert(error)
          this.$error('Houve um erro',`${error}`)
          
          }
      },
      searchDirData(value){
        try {
          return new Promise(async (resolve,reject)=>{
            
          const data = (await axios.get(`${ip}/dir/search?search=${value}`)).data

         resolve(data)



        })
            
          } catch (error) {
            reject(error)

          
          }
      },
      searchDirDataIn(value){
        try {
          return new Promise(async (resolve,reject)=>{

            
          const data = (await axios.get(`${ip}/dir/searchIn`,
          {params:{
          search:value,
          dir:this.dir

          }})).data

         resolve(data)



        })
            
          } catch (error) {
            reject(error)

       
          }
      },
      async search(value){
            try {

        
              
      this.on = true
      const data = (await this.searchDirData(value))
    //  console.log(data)
      this.archives = data.arquivos
      this.dir = data.dir
      this.curDir = data.dir.currentDir

        this.on = false
      } catch (error) {
        this.on = false
        this.$error('Houve um erro',`${error}`)
        
      }
      },
      async searchIn(value){
        try{

      this.on = true
      const data = (await this.searchDirDataIn(value))
    //  console.log(data)
      this.archives = data.arquivos
      this.dir = data.dir
     this.curDir = data.currentDir
   // console.log(data)

        this.on = false
          } catch (error) {
        this.on = false
        this.$error('Houve um erro',`${error}`)
        
      }
      },
      async download(file){
      
      try {


        window.open(`${ip}/dir/dirFile?dir=${this.dir}&file=${file}`)

        
      } catch (error) {
        this.on = false
        this.$error('Houve um erro',`${error}`)
        
      }

      },
      async onRestart(restart){

      try {
        
      await this.search(restart)
      
  
      } catch (error) {
        this.on = false
        this.$error('Houve um erro',`${error}`)
        
      }

      }
    },

   async mounted() {
     
     try {
     this.on = true
     const data = (await this.getDirData())
   //  console.log(data)
     this.archives = data.arquivos
     this.restart = this.dir = data.dir
     this.curDir = data.currentDir 

     this.on = false

     this.$show('pronto para uso!',
     'Podes acessar aos arquivos do pc ',6000,'dark')

        
      } catch (error) {
        this.on = false
        this.$error('Houve um erro',`${error}`)
        
      }



    },
  }
</script>
