<template>
    <v-container grid-list-md>
        <form @submit.prevent="enviar(files)" enctype="multipart/form-data">
        <v-card
        :disabled="loading"
            :loading="loading"
        
         max-width="600" outlined class="mx-auto rounded mb-2" elevation="5">
          <v-img
      width="100%"
      height="150" 
      lazy-src="@/assets/image/09.jpg"
      src="@/assets/image/09.jpg" />
        <div class="ma-2">      
        <v-file-input
          :disabled="loading"
          :loading="loading"
           
            @change="selectfile"
            color="primary accent-4"
            counter
            label="Selecione os arquivos que deseja enviar"
            multiple
            locale="pt"
            outlined
            placeholder=""
            prepend-icon="mdi-paperclip"
            :show-size="1000">
    <template v-slot:selection="{ index, text }">
      <v-chip
        v-if="index < 2"
        color="primary accent-4"
       
        label
        small
      >
        {{ text }}
      </v-chip>

      <span
        v-else-if="index === 2"
        class="text-overline grey--text text--darken-3 mx-2"
      >
        +{{ files.length - 2 }} arquivo(s)
      </span>
    </template>
        </v-file-input>
    <v-card-actions>
           <v-btn
           type="submit"
            :loading="loading"
            large color="success" 
             dark
             rounded >
           <v-icon >{{ icon }}</v-icon>
           </v-btn>
           <v-spacer></v-spacer>
    <div class="text-center">
    <v-progress-circular
      :rotate="360"
      :size="70"
      :width="3"
      :value="value"
      color="primary"
    >
      {{ value }}%
    </v-progress-circular>
    </div>
    </v-card-actions>
         </div>
        </v-card>
        </form>
      
    <v-simple-table elevation="3">
    <template v-slot:default>
      <thead >
        <tr>
          <th class="text-left">
            #
          </th>
          <th class="text-left">
            Nome do arquivo
          </th>
          <th class="">
            <v-icon md color="red" @click="concluido = []">mdi-trash-can-outline</v-icon>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="item in concluido"
          :key="item.name"
        >
          <td>  <v-icon color="success">mdi-check-all</v-icon></td>
          <td>{{ item.name }}</td>
          <td>
          <v-icon color="red" @click="apagar(concluido.indexOf(item))">mdi-trash-can-outline</v-icon>
          </td>
        </tr>
      </tbody>
    </template>
  </v-simple-table>
    </v-container>
</template>

<script>
import axios from 'axios'
export default {
    data:()=>({
        sort:Math.floor(Math.random() * (6 - 1 + 1)) + 1,
        dialog:false,
            title:'',
            desc:'',
        icon:'mdi-cloud-upload-outline',
        files: null,
        loading:false,
        value:0,
        concluido:[]

    }),
    refazer(){
       

    },
    methods: {
      apagar(i){
      
      this.concluido.splice(i, 1)
        
        
      },
        selectfile(event){

            this.files = event

        },

    async upload(file){
    
        return new Promise((resolve,rej)=>{
        setTimeout(() => {
        this.value = 0
            
        const formData = new FormData()
        formData.append('file',file)

        axios.post(`${this.$root.link}/upload`,formData,{onUploadProgress:(event,other)=>{
            
            this.value = Math.ceil(((event.loaded)/ event.total) * 100)
      
        }}).then((a)=>{

            
            resolve(this.files.indexOf(file))

        })

        }, 1500);



        }).then((i)=>{
        this.concluido.push(file)
        
        // this.files.sp(i,1)

        console.log(file.name)

        })
   
       
        // return new Promise((res,rej)=>{

        //     setTimeout(() => {
                
        //         res(file)
        //         console.log(file)
                
        //     }, 2000);


        // }).then( (a)=>{


        // })

        },


    async enviar(files){
        try {
        this.loading = true

        for (let index = 0; index < files.length; index++) {
       
            const element = files[index];
            await this.upload(element)
            

        }
        this.loading = false
              await this.$monstrar(await this.$carregar(5000,'dark'),
               'Seus arquivo foi enviado com sucesso ',
              `Tu enviaste no total ${files.length} arquivos!`,
              'dark')

        // this.dialog = !this.dialog 
        //    this.title = 
        //     this.desc =  
            
        


        } catch (error) {
            
            this.loading = false
            this.$error('Houve um erro!',`- ${error}`)
          //   this.dialog = !this.dialog 
          //  this.title = 'Houve um erro!' 
          //   this.desc = `- ${error}` 
            
            
        }
    }
    },
    async created(){

       

      
    //   const item = await this.$carregar(5000,'primary')

    // setTimeout(async ()=>{

    //   await this.$monstrar(item,'Pronto para Carregar','Faa j o seu carregamento!','primary')

    // },3000)

    }
    
}
</script>